<template>
	<q-page padding>
		<div class="full-width row justify-around">
			<div class="col-12">
				<q-input dense outlined v-model="text" class="q-ml-md"
				placeholder="搜尋地點" @keyup.enter="search">
					<template v-slot:append>
						<q-icon v-if="text === ''" name="search"></q-icon>
						<q-icon v-else name="clear" class="cursor-pointer" @click="clean"></q-icon>
					</template>
					<template v-slot:after>
						<q-btn @click="search" round dense flat icon="send" color="primary"></q-btn>
					</template>
				</q-input>
			</div>
		</div>
		<infinite-scroller
			v-if="showList"
			@processingLoad="onLoad"
			@selectedReataurant="setSelectedRestaurant"
		/>
		<div v-show="noReuslt" class="text-h6 text-center q-pt-md">
			No result.
		</div>
		<q-dialog v-model="isSelectedRestaurant" full-width>
			<restaurant :place-id='placeId'></restaurant>
		</q-dialog>
	</q-page>
</template>

<script>
import { filter, takeUntil } from 'rxjs/operators';
import { timer } from 'rxjs';
import { mapGetters } from 'vuex';
import InfiniteScroller from '@/components/InfiniteScroller.vue';
import Restaurant from '@/components/Restaurant.vue';

export default {
	name: 'Search',
	components: { InfiniteScroller, Restaurant },
	mounted() {
		this.$store.commit('setCurrentPath', 'search');
		this.$store.commit('setParentPath', '/search');
	},
	data() {
		return {
			text: '',
			isLoading: false,
			isSelectedRestaurant: false,
			placeId: null,
			noReuslt: false,
			showList: false,
		};
	},
	methods: {
		setSelectedRestaurant(placeId) {
			this.placeId = placeId;
			this.isSelectedRestaurant = true;
		},
		clean() {
			this.text = '';
			this.noReuslt = false;
			this.$store.commit('cleanList');
			this.showList = false;
		},
		search() {
			const searchText = this.text.trim();
			if (searchText.length > 0) {
				console.log('search');
				const params = {
					query: searchText,
					type: 'restaurant',
					radius: 500,
					language: 'zh-TW',
				};
				console.log(params);
				this.isLoading = true;
				const onSuccess = (isEmpty) => {
					this.isLoading = false;
					this.noReuslt = isEmpty;
					this.showList = true;
				};
				const onFailure = () => {
					this.isLoading = false;
					this.noReuslt = false;
					this.showList = false;
				};
				const payload = {
					params,
					onSuccess,
					onFailure,
				};
				this.$store.dispatch('searchByText', payload);
			}
		},
		onLoad(done) {
			const timer$ = timer(100, 500);
			const stop$ = timer$.pipe(filter(
				() => { return this.getNextPageToken; },
			));
			const result$ = timer$.pipe(takeUntil(stop$));
			this.$subscribeTo(
				result$,
				null,
				null,
				() => {
					const onSuccess = (hasNext = true) => {
						console.log('has next', hasNext);
						done(!hasNext);
					};
					console.log('fetch next');
					this.$store.dispatch('fetchNextPage', onSuccess);
				},
			);
		},
	},
	computed: {
		...mapGetters(['getNextPageToken', 'getRestaurants']),
	},
};
</script>
